<template>
  <el-container id="box">

    <el-main>
    <!-- <div class="home" >

      <img class="toHome" @click="goHome" src="../assets/cdl_shouye1.svg" alt="">

    </div> -->
    <div class="header">
      <div  @click="goHome" class="back-img">
        <img   src="../assets/regist/fanhui_hei_icon.png" />
        <span class="return_home">{{$t('order_list.back')}}</span>
      </div>

      <span class="title">{{$t('order_list.title')}}</span>
      <span class="refresh" >
        <img src="../assets/刷新.svg"  @click="refresh" >
      </span>
    </div>

  <el-tabs :stretch="true" v-model="activeName" stretch  @tab-click="handleClick" tab-position='top'  >

    <el-tab-pane   :label="$t('order.processing')" name="first"><underway  :process_list ="process_list"></underway></el-tab-pane>
    <el-tab-pane   :label="$t('order.completed')" name="second"><completed   :finish_list="finish_list" ></completed></el-tab-pane>
    <el-tab-pane  :label="$t('order.cancelled')" name="third"><canceled   :cancel_list="cancel_list"></canceled></el-tab-pane>

  </el-tabs>
    </el-main>
    <div id="ZZ" @touchmove.prevent v-show="showZZ"></div>
    <div id="loading" v-show="showZZ" ></div>
  </el-container>
</template>
<script>
import Underway from '../components/order/Underway.vue'
import Completed from '../components/order/Completed.vue'
import Canceled from '../components/order/Canceled.vue'
export default {
  components: {
    Underway,
    Completed,
    Canceled
  },
  data () {
    return {
      input: '',
      activeName: 'first',
      process_list:[],
      finish_list:[],
      cancel_list:[],
      showZZ:true
    }
  },
  methods: {
    handleClick (tab, event) {
    },
    goHome () {
      this.$router.push('/about')
    },
    refresh(){
      window.location.reload()
    }
  },
  mounted(){
    this.showZZ=true
    let login =false
    let userId = 0
    let userInfo = null
    if(localStorage.userInfo){
       login = (JSON.parse(localStorage.userInfo))
       userId = login.data.id
	   userInfo = login
    }else{
      login = false
    }

    if(login){
		 axios.defaults.headers.common["Authorization"] = `${userInfo.token_type}\x20${userInfo.access_token}`
          axios(`https://jzk80vaqs7.execute-api.ap-northeast-1.amazonaws.com/dev/get_order_list_customer/v1?cust_id=${userId}`)
          .then(res=>{
           this.showZZ=false
              console.log(res.data)
              this.process_list = res.data.process_list.reverse()
              this.finish_list = res.data.finish_list.reverse()
              this.cancel_list = res.data.cancel_list.reverse()
          }).catch(err=>{
           this.showZZ=false
           this.$router.push('/login')
            console.log(err.response)
          })
    }else{
      alert(this.$t('error.please_login'))
      this.$router.push('/login')
    }
  }
}
</script>
