<template>
  <div class="box">
    <div class="head">
      <img class="cha" src="../assets/zc_cha.svg" @click="hide" />
    </div>
    <div class="options" @click="changeSelected(81)" >
        <span class="left">
        <img class="flag" src="../assets/countries/zc_riben.png" />
        <span class="font">日本(JP+81)</span>
        </span>

        <img v-show="select==81"  class="selected"  src="../assets/countries/zc_dagou_icon.png" />
    </div>

    <div class="options" @click="changeSelected(86)">
      <span class="left">
        <img class="flag" src="../assets/countries/zc_china.png" />
        <span class="font">中国(CN+86)</span>
        </span>
        <img  v-show="select==86"  class="selected"  src="../assets/countries/zc_dagou_icon.png" />
    </div>
  </div>
</template>

<script>
  export default{
    props:['showSelectCountry'],
    data(){
      return{
        select:'81'
      }
    },
    methods:{
      changeSelected(value){
          this.select = value
          this.value = value
      },
      hide(){
        this.$emit('hide')
       // console.log(this.showSelectCountry)
      }
    },
    computed:{
      value: {
        get() {
          return this.$store.state.areaCode
        },
        set(value) {
          this.$store.commit('updataAreaCode', value)

        },
      }
    }
  }
</script>

<style scoped>
   .box{
     box-sizing: border-box;
     background-color: white;
     position: relative;
     z-index: 10000;
     padding: 0.8rem;
     top: 10%;
     height: 90%;
     border-top-left-radius: 0.533333rem;
     border-top-right-radius:0.533333rem;
   }
   .head{
     height: auto;
     margin-bottom: 0.8rem;
   }
   .cha{
     width: 0.48rem;
     height: 0.48rem;
   }
   .options{
     padding: 0.533333rem 0;

     height: auto;
     display: flex;
     display: -webkit-flex;
     border-bottom: 0.026666rem #E8EEEC solid ;
     align-items: center;
     justify-content: space-between;
   }
   .flag{
     width: 0.853333rem;
     height: 0.586666rem;
     margin-right: 0.4rem;
   }
   .font{
     font-size: 0.426666rem;

   }
   .selected{
     width: 0.48rem;
     height: 0.32rem;
   }
   .left{
      display: flex;
      display: -webkit-flex;

   }
</style>
