<template>
  <div class="order-goods-box">
    <div class="order-title"> {{$store.state.shopInfo.name}}</div>

    <div class="goods" v-for="data in $store.state.cartInfo" :key="data.id">

      <img class="goods-img" :src="data.oss_url" />
      <div class="goods-head">
        <span class="goods-name">{{data.name || data.set_name}}</span>
        <span>
          <span class="goods-price-unit">￥</span>
          <span class="goods-price" v-if="$store.state.diningStyle=='堂食'">{{data.pickPrice}}</span>
           <span class="goods-price" v-if="$store.state.diningStyle=='自提'">{{data.pickPrice}}</span>
        </span>
      </div>
    
      <div class="goods-num">
        {{'X'+data.num}}
      </div>
    </div>

    <div class="tips">
      *{{$t('order.tax_tip')}}
    </div>
    <div class="label">
      <span class="first-label">{{$t('order.all_product_count')}}</span>
      <span class="last-label">{{$store.state.totalGoods}}</span>
    </div>
  
    <div class="label">


      <span v-if="$store.state.diningStyle=='堂食'" class="first-label">10%{{$t('order.target')}}</span>
      <span v-if="$store.state.diningStyle=='堂食'" class="last-label">¥{{$store.state.totalPrice}}(¥{{tax}})</span>


        <span v-if="$store.state.diningStyle=='自提'" class="first-label">8%{{$t('order.target')}}</span>
        <span v-if="$store.state.diningStyle=='自提'" class="last-label">¥{{$store.state.totalPrice}}(¥{{tax}})</span>

    </div>
   
    <div class="dash"></div>
   <div class="tax">¥{{totalPrice}}</div>

    <div class="label">
      <span class="first-label">{{$t('order.total_price')}}</span>
      <span>



        <span v-if="$store.state.diningStyle=='堂食'" class="no-tax">（{{$t('order.no_tax')}}¥{{$store.state.totalPrice-tax}}）</span>
         <span v-if="$store.state.diningStyle=='自提'" class="no-tax">（{{$t('order.no_tax')}}¥{{$store.state.totalPrice-tax}}）</span>
      </span>
    </div>
  </div>

</template>

<script>
  export default{

    computed:{
      tax:{
        get(){
          return this.$store.state.tax
        },
        set(value){
          this.$store.commit('setTax',value)
        }
      },
	  totalPrice:{
		  get(){
			  return this.$store.state.totalPrice
		  }
	  }
    },
    mounted() {
        if(this.$store.state.diningStyle=='堂食'){

          this.tax = this.$store.state.totalPrice * 1 / 11
          if(this.tax>0&&this.tax<1){
            this.tax =1
          }else{
            this.tax = Math.floor(this.tax)
          }
        }
        if(this.$store.state.diningStyle=='自提'){
          this.tax = this.$store.state.totalPrice * 8 / 108
          if(this.tax>0&&this.tax<1){
            this.tax =1
          }else{
            this.tax = Math.floor(this.tax)
          }
        }
        console.log(this.tax)
    }
  }
</script>

<style scoped>
  div{
    width: auto;
    height: auto;
    box-sizing: border-box;
  }
  .order-goods-box {
    padding: 0.266666rem 0.533333rem;

  }

  .order-title {
    font-size: 0.426666rem;
    font-family: PingFang SC;
    font-weight: bold;
    line-height: 0.586666rem;
    color: #000000;
    border-left: 0.133333rem solid #00CB88;

    padding-left: 0.133333rem;

    margin-bottom: 0.4rem;
  }

  .goods {
    height: 2.453333rem;

    margin-bottom: 0.4rem;
    background: #FFFFFF;
    padding: 0.32rem;
  }

  .goods-img {
    height: 1.813333rem;
    width: 1.813333rem;
    border-radius: 10px;
    float: left;
    margin-right: 0.32rem;
  }

  .goods-head {
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
  }

  .goods-name {
    font-size: 0.373333rem;
    font-family: PingFang SC;
    font-weight: bold;
    line-height: 0.533333rem;
    color: #3F3C3C;
  }

  .goods-price-unit {
    font-size: 0.4rem;
    font-family: Bebas;
    font-weight: 400;
    line-height: 0.533333rem;
    color: #3F3C3C;
  }

  .goods-price {
    font-size: 0.453333rem;
    font-family: Bebas;
    font-weight: bold;
    line-height: 0.613333rem;
    color: #3F3C3C;
  }

  .goods-size {
    display: flex;
    display: -webkit-flex;
    margin: 0.133333rem 0;
    font-size: 0.32rem;
    font-weight: 400;
    line-height: 0.426666rem;
    color: #888888;
  }

  .goods-num {
    font-size: 0.373333rem;
    font-family: PingFang SC;
    font-weight: 500;
    line-height: 0.533333rem;
    color: #3F3C3C;
  }

  .tips {
    color: #FF6E39;
    font-size: 0.32rem;
    margin-bottom: 0.4rem;
  }

  .label {
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
    margin-bottom: 0.4rem;
  }

  .first-label {
    color: #888888;
    font-size: 0.373333rem;
    font-family: PingFang SC;
    font-weight: 600;
    line-height: 0.533333rem;
  }

  .last-label {
    font-size: 0.373333rem;
    font-family: PingFang SC;
    font-weight: 600;
    line-height: 0.533333rem;
    color: #3F3C3C;
  }

  .tax {
    text-align: right;
    font-size: 0.48rem;
    font-family: PingFang SC;
    font-weight: 500;
    line-height: 0.533333rem;
    color: #FF6E39;
    padding-right: 0.533333rem;
  }

  .no-tax {
    font-size: 0.373333rem;
    font-family: PingFang SC;
    font-weight: 400;
    line-height: 0.533333rem;
    color: #A0A0A0;
  }

  .dash {
    margin-bottom: 0.4rem;
    height: 0;
    border: 2px dashed #cfcfcf;
  }
</style>
