<template>
    <div id="order-confirm">
        <div class="header">
          <img  @click="back" class="back-img" src="../assets/regist/fanhui_hei_icon.png" />
          <span class="title">{{$t('order.order_confirm')}}</span>
        </div>

        <ordergoods></ordergoods>

         <div class="separator"></div>

         <div class="order-info-box">
            <div class="order-title"> {{$t('order.order_detail')}}</div>

            <div class="dining-style-box">
              <span class="dining-style-title">{{$t('order.dining_way')}}</span>
              <span class="dining-style about"  v-if="dining_way==0" >{{$t('shop.dining_here')}}</span>
              <span class="dining-style takeaway" v-if="dining_way==1">{{$t('shop.take_away')}}</span>
            </div>
            <div class="notes-box">
              <div class="notes-title">{{$t('order.order_notes')}}</div>
               <textarea class="notes-content" maxlength="150" v-model="value" ></textarea>
            </div>
            <div class="dining-style-box receipt-box">
              <span class="dining-style-title">{{$t('order.invoice')}}</span>
              <span class="dining-style-title receipt">{{$t('order.invoice_tip')}}</span>

            </div>
            <div class="footer">
                <span>
                  <span class="footer-title">{{$t('order.total_price')}}：¥</span>

                  <span class="footer-price">{{ total_price }}</span>
                </span>
                <span class="footer-button" :class="{'disabled':total_price==0}"  @click="toPay">{{$t('shop.checkout')}}</span>
            </div>
         </div>
         <div @touchmove.prevent v-show="showZZ" id="ZZ"></div>

    </div>

</template>
<script>
  import ordergoods from '../components/order/OrderGoods.vue'
export default {
  data () {
    return {
      input1: '',
      input2: '',
      checked1: true,
      checked2: true,
      order_info:[],
      showZZ:false,
      userInfo:JSON.parse(localStorage.getItem('userInfo'))
    }
  },
  methods: {
    confirmpay () {
      this.showZZ = true
        axios.defaults.headers.common["Authorization"] = `${this.userInfo.token_type}\x20${this.userInfo.access_token}`
        axios.post('https://jzk80vaqs7.execute-api.ap-northeast-1.amazonaws.com/dev/create_order/v1',{
           operate_mode : "create",
           order_info:this.order_info,
           order_overview:{
                    "cust_id" : this.userInfo.data.id,  //顾客ID
                    "disc_num" : 0.00,  //优惠金额
                    "order_price" : this.total_price,
                    "order_total_price" : this.total_price,
                    "pay_currency" : "JPY",
                    "pay_method" : 0,   //线上
                    "dining_way":this.dining_way,
                    "pay_platform" : "paypay",
                    "payment" : this.total_price,
                    "paypal_price" : 0,
                    "qbay_price" : 0,
                    "shop_id" : this.shop_id,
                    "shop_name" : this.shop_name,
                    "status" : 0,
                    "tax" : this.tax,
                    "text" : this.value
           }
        }).then(res=>{
          console.log(res.data,1231231)
         //  this.userInfo = JSON.parse(localStorage.getItem('userInfo'))
            this.$store.commit('clearCart')
            this.$store.commit('clearListNum')
          alert(this.$t('order.cheackout_success'))

          this.$router.push('/orderlist')
        }).catch(err=>{
              console.log(err.response)
              //如果没有记住密码
           if(err.response.data.code==1004 && !localStorage.remember){
             console.log('登录过期')
              alert(this.$t('order.login_expired'))
              this.$router.push('/login')
           }
           //如果记住密码
           else if(err.response.data.code==1004 && localStorage.remember=='true'){
                console.log('我记住了密码,签发新token')
                axios.post("https://jzk80vaqs7.execute-api.ap-northeast-1.amazonaws.com/dev/oauth2_token/v1",{
                   "refresh_token":`${this.userInfo.refresh_token}`,
                      "grant_type":"refresh_token"
                }).then(res=>{
                      console.log('重新签发了TOKEN')
                      localStorage.removeItem('userInfo')
                     
                      localStorage.setItem('userInfo',JSON.stringify(res.data))

                        this.userInfo = JSON.parse(localStorage.getItem('userInfo'))
                     setTimeout(()=>{
                       console.log('重新获取数据')
                        console.log(this.userInfo,'haha1')
                        this.confirmpay()
                     },1000)


                }).catch(err=>{
                   console.log(this.userInfo,'haha2')
                       alert(this.$t('order.login_expired'))
                      this.showZZ = false
                      this.$router.push('login')
                })
           }else{
              console.log(err.response.data.code==1004,localStorage.remember==true)
               this.$router.push('/login')
           }

        })
    },
    setOrderInfo(){
        for(let i of this.$store.state.cartInfo){
            let obj = {
              "prod_id" : parseInt(i.id) ,
              "item_real_price" : i.pickPrice,
              "item_price" :  i.pickPrice,
              "prod_name" : i.name,
              "product_logo_url":i.oss_url ,
              "quantity" : i.num,
              "size": i.selectedSize,
            }
            this.order_info.push(obj)
        }
    },
    goHome () {
      this.$router.push('/')
    },
    back(){
      this.$router.push('/about')
    },
    toPay(){
        if(this.total_price==0){
          return
        }
        this.confirmpay()


    //  this.$router.push('/orderdetail')
    },

  },
  mounted(){
    this.setOrderInfo()
  },
  beforeDestroy() {
    this.$store.commit('updataOrderNote','')
  },
  computed:{
    value:{
      get(){
        return this.$store.state.orderNote
      },
      set(data){
        this.$store.commit('updataOrderNote',data)
      }
    },
    shop_id:{
      get(){
        return parseInt(this.$store.state.shopInfo.id)
      }
    },
    shop_name:{
      get(){
        return this.$store.state.shopInfo.name
      }
    },
    dining_way:{
      get(){
          if(this.$store.state.diningStyle=='堂食'){
              return 0
          }else{
            return 1
          }
      }
    },
    total_price:{
      get(){
        return (this.$store.state.totalPrice ).toString()
      }
    },

    // userInfo:{
    //   get(){
    //     return JSON.parse(localStorage.getItem('userInfo'))
    //   },


    // },
    tax:{
      get(){
        return this.$store.state.tax
      }
    }

  },
  components:{
    ordergoods,
  }
}
</script>

