<script>
  // import googleLogin from '../components/googleLogin.vue'
  // import LineLoginButton from '../components/LineLoginButton.vue'
  export default {
    components: {
      // googleLogin,
      // LineLoginButton
    },
    data() {
      return {
        showZZ: false,
        telNumber: '',
        password: '',
        checked: false,
        showAlert: false,
        options: [{
          value: '81',
          label: '81'
        }, {
          value: '86',
          label: '86'
        }, ],
        value: '81'
      }
    },
    methods: {
      isShowZZ() {

        this.showZZ = true
      },
      toRegist() {
        this.$router.push('/regist')
        console.log('去注册界面')
      },
      appleLogin() {
        console.log('appleLogin')
      },
      googleLogin() {
        console.log('googleLogin')
      },
      lineLogin() {
        console.log('lineLogin')
      },
      send() {
        alert('第三方登录')
      },
      goHome() {
        this.$router.push('/')
      },
      forget() {
        this.$router.push('/forgetpassword')
      },
      signup() {
        this.$router.push('/regist')
      },
      english() {
        this.$router.push('/languageswitch')
      },
      gots() {
        this.$router.push('/orderconfirm')
      },
      login() {
        if (!this.telNumber || !this.password) {
          alert(this.$t('login.userNameErrMsg'))
          return
        }

        let email = ''
        if (this.telNumber.includes('@')) {
          email = this.telNumber
        } else {
          //不是邮箱
          //是日本手机号
          if (this.telNumber[0] == 0 || this.telNumber[0] == 7 || this.telNumber[0] == 8 || this.telNumber[0] == 9) {
            if (this.telNumber[0] == 0) {
              email = `81${this.telNumber.substr(1)}@mail.qbaytek.com`
            } else {
              email = `81${this.telNumber}@mail.qbaytek.com`
            }
          } else {
            email = `86${this.telNumber}@mail.qbaytek.com`
          }


        }

        this.showZZ = true
        axios.post(
          "https://jzk80vaqs7.execute-api.ap-northeast-1.amazonaws.com/dev/oauth2_token/v1", {
            "email": email,
            "password": `${this.password}`,
            "grant_type": "password"
          }
        ).then(res => {
          this.showZZ = false
          //console.log(res.data)
          // this.$store.commit('userInfo', res.data)
          //  console.log(thi s.$store.state.userInfo)

          localStorage.setItem('userInfo', JSON.stringify(res.data))

          localStorage.setItem('remember', !this.checked)

          alert(this.$t('login.login_success'))

          this.$router.push('/about')
          //this.gots()

        }).catch(err => {
          this.showZZ = false
          console.log(err.response)
          if (err.response.data.code == 2004) {
            alert(this.$t('error.emailErr'))
          } else if (err.response.data.code == 2006) {
            alert(this.$t('error.passwordErr'))
          } else {
            alert(this.$t('error.unknownErr'))
          }

        })
      },

      async toHome() {
        try {
          let res = await this.login()
        } catch (err) {
          console.log(err)
        }


      },
      remeberMe() {
        this.checked = !this.checked
      },

    }
  }
</script>
