<template >
  <div style="background-color: #f3f3f3;" >
	<div v-if="process_list.length">
    <div class="coupons-wrapper" v-for="data in process_list" :key="data.id" @click="toOrderDetail(data.id)">
      <div class="head">
        <div class="left">
          <img class="shop-icon" src="../../assets/0.png" alt="">
          <span class="shop-name">{{data.shop_name}}</span>
          <img class="more" src="../../assets/dd_gengduo.svg" alt="">
        </div>
        <div class="right">
          <span class="prepare" v-show="data.status==1 && data.schedule==1" >{{$t('order_list.prepare')}}</span>
          <span class="wait" v-show="data.status==1 && data.schedule==2" >{{$t('order_list.wait')}}</span>
          <span class="not-accepted" v-show="data.status==0 && data.schedule==0">{{$t('order_list.not_received')}}</span>
        </div>
      </div>
      <div class="content">
        <div class="imgs">

            <div class="img-box" v-for="good in data.order_info.slice(0,3)" :key="good.prod_id+good.size">
            <img class="goods-icon" :src="good.product_logo_url"   alt="">
            <span class="good-name" >
              {{good.prod_name}}
            </span>
          </div>

        </div>
        <div class="goods-total">
          <span class="price">￥{{data.order_total_price}}</span>
          <span class="counts">{{$t('order_list.total')}} {{data.order_info | total_goods }} {{$t('order_list.item')}}</span>
        </div>
      </div>

      <div class="line"></div>

      <div class="take-number">
        <span class="text">{{$t('order_list.take_number')}}:</span>
        <span class="number">{{data.number}}</span>
      </div>
    </div>
	</div>
  <div v-if="process_list.length==0">
     没有订单信息
  </div>

  </div>
</template>
<script>
  import Vue from 'vue'
  Vue.filter('total_goods',(data)=>{
      let total = 0
      for(let i of data){
          total += i.quantity
      }

     return total
  })
  export default{
    props:['process_list'],
    mounted() {
      console.log(this.process_list)
    },
    methods:{
        toOrderDetail(id){
          console.log('去订单详情页面')
          console.log(id)
          axios(`https://jzk80vaqs7.execute-api.ap-northeast-1.amazonaws.com/dev/get_order_info/v1?order_id=${id}`)
          .then(res=>{
              console.log(res.data)
              this.$store.commit('setOrderInfo',res.data)
              this.$router.push("/orderdetail")
          })
          .catch(err=>{
            console.log(err.response)
          })
        //
        }
    },
  }
</script>
