<template>
  <div id="order-detail">

    <div class="header">
      <img @click="back" class="back-img" src="../assets/regist/fanhui_hei_icon.png" />
      <span class="title">{{$t('order_detail.title')}}</span>
    </div>

      <orderprocess v-if="ordersInfo.status!=-1" :schedule="ordersInfo.schedule" >   </orderprocess>

      <ordercancel  v-if="ordersInfo.status==-1">  </ordercancel>


    <div class="separator" v-if="ordersInfo.status!=-1" ></div>

    <div class="take-number" v-if="ordersInfo.status!=-1">
        <span class="number-text"> {{$t('order_list.take_number')}}:</span>
        <span class="numbers"> {{ordersInfo.number}}</span>
    </div>

  <div class="separator"></div>
    <div class="order-goods-box">
      <div class="order-title"> {{ordersInfo.shop_name}}</div>

      <div class="goods" v-for="data in ordersInfo.order_info" :key="data.prod_id+data.size">

        <img class="goods-img" :src="data.product_logo_url" />
        <div class="goods-head">
          <span class="goods-name">{{data.prod_name}}</span>
          <span>
            <span class="goods-price-unit">￥</span>


             <span class="goods-price">{{data.item_real_price}}</span>

          </span>
        </div>
       
        <div class="goods-num">
          {{'X'+data.quantity}}
        </div>
      </div>

      <div class="tips">
       *{{$t('order.tax_tip')}}
      </div>
      <div class="label">
        <span class="first-label">{{$t('order.all_product_count')}}</span>
        <span class="last-label">{{ordersInfo.order_info | total_goods}}</span>
      </div>
      <div class="label">

        <span v-if="!ordersInfo.dining_way" class="first-label">10%{{$t('order.target')}}</span>
        <span v-if="!ordersInfo.dining_way" class="last-label">¥{{ordersInfo.order_total_price}}(¥{{ordersInfo.tax}})</span>

        <span v-if="ordersInfo.dining_way" class="first-label">8%{{$t('order.target')}}</span>
        <span v-if="ordersInfo.dining_way" class="last-label">¥{{ordersInfo.order_total_price }}(¥{{ordersInfo.tax}})</span>
      </div>
    
    

      <div class="dash"></div>

       <div class="tax">¥{{ordersInfo.order_total_price }}</div>
      <div class="label">
        <span class="first-label">{{$t('order.total_price')}}</span>
        <span>

          <span class="no-tax">（{{$t('order.no_tax')}}¥{{ordersInfo.order_total_price-ordersInfo.tax}}）</span>
        </span>
      </div>
    </div>



    <div class="separator"></div>

    <div class="order-info-box">

      <div class="order-title"> {{$t('order.order_detail')}}</div>

      <div class="dining-style-box">
        <span class="dining-style-title">{{$t('order_detail.order_number')}}</span>
        <span class="dining-style">
          {{orderId}}
        </span>
      </div>
      <div class="dining-style-box">
        <span class="dining-style-title">{{$t('order.dining_way')}}</span>
        <span  v-show="diningWay" class="dining-style takeaway">{{$t('shop.take_away')}}</span>
         <span v-show="!diningWay" class="dining-style about" >{{$t('shop.dining_here')}}</span>
      </div>
      <div class="dining-style-box">
        <span class="dining-style-title">{{$t('order_detail.order_time')}}</span>
        <span class="dining-style">{{orderDate}}</span>
      </div>
      <div class="notes-box">
        <div class="notes-title dining-style-box">
          <span>{{$t('order.order_notes')}}</span>
          <span class="dining-style">{{orderTime}}</span>
        </div>
        <textarea readonly class="notes-content" v-model="text" maxlength="150" placeholder=""></textarea>
      </div>


    </div>

  </div>

</template>
<script>
   import orderprocess from '../components/order/OrderProcess.vue'
   import ordergoods from '../components/order/OrderGoods.vue'
   import ordercancel from '../components/order/OrderCancel.vue'
   import Vue from 'vue'
   Vue.filter('total_goods',(data)=>{
       let total = 0
       for(let i of data){
           total += i.quantity
       }

      return total
   })
  export default {
    data() {
      return {
        input1: '',
        input2: '',
        checked1: true,
        checked2: true,
      }
    },
    methods: {

      goHome() {
        this.$router.push('/')
      },
      back() {
        this.$router.push('/orderlist')
      }
    },
    components:{
      orderprocess,
      ordergoods,
      ordercancel
    },
    computed:{
      orderId:{
        get(){
          return this.$store.state.ordersInfo.id
        }
      },
      orderDate:{
        get(){
          return this.$store.state.ordersInfo.order_date.substring(0,10)
        }
      },
      orderTime:{
        get(){
          return this.$store.state.ordersInfo.order_date.substring(10,19)
        }
      },

      diningWay:{
        get(){
          return this.$store.state.ordersInfo.dining_way
        }
      },
      text:{
        get(){
          return this.$store.state.ordersInfo.text
        }
      },
      ordersInfo:{
        get(){
          return this.$store.state.ordersInfo
        }
      }

    },



  }
</script>

