<template>
  <div id="tel-regist">

    <img class="return" @click="return1" src="../assets/regist/fanhui_hei_icon.png" />
    <img class="qbay-logo" src="../assets/qbay_logo.svg">
    <div class="registLabel">{{$t("regist.register_with_phone")}}</div>


    <span class="label-tel">{{$t("tel.sign_in_hint_phone")}}</span>


    <!-- <el-select v-model="value" class="quhao">
      <el-option class="quhao1" v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
      </el-option>
    </el-select> -->
    <div class="quhao" @click="showSelectCountry=true;isShow=true">
      <img v-show="value==81" class="flag" src="../assets/countries/zc_riben.png" />
      <img v-show="value==86" class="flag" src="../assets/countries/zc_china.png" />
      <img class="jiantou" src="../assets/countries/jiantou.png" />
    </div>

    <input class="tel" type="tel" v-model="telnumber" :placeholder="$t('tel.sign_in_hint_phone')">
    <img src="../assets/zc_cha.svg" class="delete" v-show="telnumber" @click="clearTelNumber">



    <span class="label-captcha">{{$t('tel.verify_code')}}</span>
    <input class="captcha" type="text" maxlength="6" v-model="captcha" :placeholder="$t('tel.sign_in_hint_verify_code')">

    <button @click="getCaptcha" class="get-captcha" :class="{'disabled':!telnumber||send}" :disabled="!telnumber||send">

      {{msg | disabled(this.countdown)}}

    </button>


    <img class="next" src="../assets/regist/zc_xiayibu2_botton.png" v-show="!telnumber|| captcha.length!=6">

    <img class="next" src="../assets/regist/zc_xiayibu1_botton.png" v-show="telnumber && captcha.length==6" @click="next" />

    <span v-show="isError" class="captcha-err">
      <sup>*</sup>
      {{$t('tel.incorrect_verify_code')}}
    </span>

    <label class="menu_label2" @click="toLogin">
      <span> {{$t('login.loginTitle')}}</span>
    </label>
    <div id="ZZ" v-show="isShow"></div>
    <transition enter-active-class="animate__animated animate__fadeInUp" leave-active-class="animate__animated animate__fadeOutDown"
      :duration="{ enter: 500, leave: 200 }" mode="out-in">
      <selectCountry @hide="hide" v-show="showSelectCountry"></selectCountry>
    </transition>
  </div>
</template>
<script>
  import Vue from 'vue'
  import selectCountry from '../components/SelectCountry.vue'
  Vue.filter('disabled', (data, time) => {
    if (time < 60) {
      return time + 's'
    } else {
      return data
    }
  })
  export default {

    data() {
      return {
        showAlert: false,
        showAlert2: false,
        isError: false,
        captchaInterval: null,
        countdown: 60,
        msg: this.$t('regist.send'),
        isShow: false,
        send: false,
        showSelectCountry: false,
        options: [{
          value: '86',
          label: 'CN+86'
        }, {
          value: '81',
          label: 'JP+81'
        }, ],
      }
    },
    methods: {
      toLogin() {
        this.$router.push('/login')
      },
      return1() {
        this.$router.push('/login')
      },
      clearTelNumber() {
        this.telnumber = ''
      },
      hide() {
        this.showSelectCountry = false
        this.isShow = false
      },
      next() {
        this.isShow = true
        console.log('验证验证码')
        let tel = ''
        if (this.telnumber[0] == 0) {
          tel = this.telnumber.substr(1)
        } else {
          tel = this.telnumber
        }
        axios.post('https://jzk80vaqs7.execute-api.ap-northeast-1.amazonaws.com/dev/phone_signup/v1', {
          "phone": `${this.value}${tel}`,
          "code": `${this.captcha}`
        }).then(res => {
          console.log(res.data)
          this.isShow = false
          this.$router.push('/setpassword')
        }).catch(err => {
          console.log(err.response)
          alert(this.$t("tel.incorrect_verify_code"))
          this.isShow = false
          this.isError = true

        })

        //this.$router.push('/setpassword')
      },

      setCaptchaInterval() {
        this.send = true

        this.captchaInterval = setInterval(() => {
          this.countdown--
          if (this.countdown == 0) {
            clearInterval(this.captchaInterval)
            this.countdown = 60
            this.send = false
          }
          console.log(this.countdown)
        }, 1000)

      },

      getCaptcha() {

        this.send = true
        this.isShow = true
        let tel = ''
        if (this.telnumber[0] == 0) {
          tel = this.telnumber.substr(1)
        } else {
          tel = this.telnumber
        }
        axios.post('https://jzk80vaqs7.execute-api.ap-northeast-1.amazonaws.com/dev/phone_register/v1', {
          "phone": `${this.value}${tel}`
        }).then(res => {
          this.isShow = false
          console.log(res.data)
          console.log(121313)
          alert(this.$t('tel.send_code_success'))
          this.setCaptchaInterval()
        }).catch(err => {
          this.isShow = false
          clearInterval(this.captchaInterval)
          this.captchaInterval = null
          this.send = false

          if (err.response.data.code == 3007) { //验证发送失败
            alert(this.$t('email.email_sent_failed'))
          } else if (err.response.data.code == 2006) { //手机号已被注册
            alert(this.$t('tel.phone_registered_yet'))
          } else {

            // alert(`${err.response.data.message}`)
            alert(this.$t('error.unknownErr'))
          }

          //  alert(`${err.response.data.message}`)
          console.log(err.response)
        })
      },
    },

    beforeDestroy() {

      clearInterval(this.captchaInterval)
      this.captchaInterval = null
    },
    watch: {
      showAlert2(newValue, oldValue) {

        if (!newValue && oldValue) {
          this.$router.push('/login')
        }
      }
    },
    components: {
      selectCountry
    },
    computed: {
      // msg:{
      //   get(){
      //     return
      //   }
      // },
      telnumber: {
        get() {
          return this.$store.state.telNumber
        },
        set(value) {

          this.$store.commit('updataTelNumber', value)
          this.isError = false
        }
      },
      captcha: {
        get() {
          return this.$store.state.captcha
        },
        set(value) {
          this.$store.commit('updataCaptcha', value)
          this.isError = false
        },

      },
      value: {
        get() {
          return this.$store.state.areaCode
        },
        set(value) {
          this.$store.commit('updataAreaCode', value)

        },
      }
    }
  }
</script>
