<template>
  <div>

    <img class="return" @click="return1" src="../assets/regist/fanhui_hei_icon.png" />
      <img class="qbay-logo" src="../assets/qbay_logo.svg" >

    <div class="registLabel">{{$t("regist.register_with_email")}}</div>


    <span class="label">{{$t("login.emailPlaceholder")}}</span>

    <input class="email" type="email" v-model="email" :placeholder="$t('login.emailErrMsg')">

    <button @click="sendEmail" class="send-email">{{$t('login.send_registration_email')}}</button>

    <label class="menu_label2" @click="toLogin">
      <span> {{$t('login.loginTitle')}}</span>
    </label>



    <div   @touchmove.prevent id="ZZ" v-show="showAlert "></div>
  </div>
</template>
<script>
  import { MessageBox } from 'mint-ui';
  export default {

    data() {
      return {
        input1: '',
        showAlert: false,
        showAlert2: false,
        email: '',
      }
    },
    methods: {
      toLogin() {
       this.$router.push('/login')
      },
      return1() {
        this.$router.push('/regist')
      },


      sendEmail() {
        if(!this.email || !this.email.includes('@')){
          alert(this.$t("error.email_err"))
          return
        }
        this.showAlert = true
        // alert('邮件已发送')
        axios.post('https://jzk80vaqs7.execute-api.ap-northeast-1.amazonaws.com/dev/send_register_account_email/v1', {
          "email": `${this.email}`
        }).then(res => {
          console.log(res)
          if (res.status === 200) {

            alert(this.$t("login.send_email_success"))
             this.$router.push('/login')
          }
        }).catch(err => {
          console.log(err.response)

          // this.showAlert = true
          if (err.response.data.code === 2003) { //错误返回信息
               alert(this.$t("error.email_err"))
          }else if(err.response.data.code === 2004){
			   alert(this.$t("email.has_user"))
		  }
		  else{
               alert(this.$t("email.email_sent_failed"))
          }
            this.showAlert = false
        })
      }
    },
    watch: {
      showAlert2(newValue, oldValue) {

        if (!newValue && oldValue) {
          this.$router.push('/login')
        }
      }
    }
  }
</script>
